.Home {
  &__Title {
    font-size: 32px;
    font-weight: bold;
    color: #333333;
  }
  &__Pointers {
    margin: 0 auto;
    font-size: 16px;
    width: 60%;
    // border: 1px solid red;

    &--pointer {
      display: flex;
      align-items: center;
      text-align: left;
      font-size: 20px;
      margin: 40px 0;
    }
  }

  &__Lack {
    margin: 50px auto 20px auto;
    width: 80%;
    font-weight: 600;
    font-size: 18px;

    &--change {
      margin-top: 20px;
      font-size: 20px;
    }
  }

  &__Learn {
    margin: 60px 0 40px 0;
    color: #4e64c8;
    font-weight: bold;
    cursor: pointer;
  }
}
